// components/ProtectedRoute.js
import { useRouter } from "next/router";
import { useEffect, useState } from "react";

export default function ProtectedRoute({ user, children }) {
  const router = useRouter();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // S√≥ depois de checar o user definimos o loading
    if (user === undefined) return; // ainda n√£o inicializou
    if (!user) {
      router.replace("/login"); // redirect apenas quando user √© null
    } 
    setLoading(false);
  }, [user, router]);

  // Enquanto carregando, n√£o renderiza nada
  if (loading) return <div style={{ padding: 40 }}>Carregando...</div>;

  // Se user existe, renderiza children
  return <>{user ? children : null}</>;
}